import React, { Fragment, Suspense, Component } from 'react';
import '../Styles/CreateFood.css';
import { wait, toEnglishNumber, Snackbar, snackbarHandle, introPageTransition } from '../dependencies';
import { S } from 'selector_dom';
import { addedByUsers, data, userData, storeData, uploadCreatedFood, userUid } from '../Data';
import { updateRenderState } from '../index';

import NavicationBar from '../NavicationBar/NavicationBar';
const FavoritePage = React.lazy(() => import('../NavicationBar/FavoritePage'));

let tmpFood;
let firstRender = true;

export default class CreateFood extends Component {
  UNSAFE_componentWillMount() {
    if (firstRender) {
      window.location.pathname.includes('/EditFood')
        ? (tmpFood = addedByUsers.filter(e => e.id === this.props.id)[0])
        : (tmpFood = {
            id:
              addedByUsers.length !== 0
                ? 'addedByUsers' + (Number(addedByUsers[addedByUsers.length - 1].id.replace(/\D/g, '')) + 1)
                : 'addedByUsers0',
            name: '',
            brand: '',
            description: 'آخر',
            serving: [['حصة قياسية (100 غ)', 100]],
            kcal: 0,
            fat: 0,
            saturatedFat: 0,
            unsaturatedFat: 0,
            carb: 0,
            fiber: 0,
            sugar: 0,
            protein: 0,
            cholesterol: 0,
            sodium: 0,
            potassium: 0,
          });
    }
    firstRender = false;
  }

  componentDidMount() {
    introPageTransition('createFoodPage');
    this.centerSelect('chooseCategorie');
  }

  componentWillUnmount() {
    firstRender = true;
  }

  centerSelect = async id => {
    const element = document.getElementById(id);
    const newDiv = document.createElement('p');
    newDiv.classList.add('centerSelect');
    newDiv.innerHTML = element.options[element.selectedIndex].innerHTML;
    document.body.appendChild(newDiv);
    const getWidth = parseFloat(window.getComputedStyle(newDiv).getPropertyValue('width')) / 2 + 'px)';
    newDiv.remove();

    element.style.width = '0px';
    await wait(20);
    element.style.removeProperty('width');
    element.style.paddingRight = 'calc(50% - ' + getWidth;
  };

  doneHandle = async () => {
    if (tmpFood.name.length === 0) {
      snackbarHandle('الرجاء ادخال اسم الطعام', 'error');
      S('#foodNameContainer').css({ borderColor: '#f44336' });
    } else if (window.location.pathname.includes('/EditFood')) {
      const indexFormAdded = addedByUsers.findIndex(e => e.id === this.props.id);
      const indexFormData = data.findIndex(e => e.id === this.props.id);
      addedByUsers[indexFormAdded] = tmpFood;
      data[indexFormData] = tmpFood;

      // firebase
      const clone = Object.assign({}, tmpFood, { serving: tmpFood.serving ? tmpFood.serving[0][1] : 100 });
      uploadCreatedFood(`${tmpFood.name} [${userUid}]`, clone);

      window.history.back();
    } else {
      addedByUsers.push(tmpFood);
      data.push(tmpFood);
      userData.favorite.food.unshift(tmpFood.id);

      // firebase
      const clone = Object.assign({}, tmpFood, { serving: tmpFood.serving ? tmpFood.serving[0][1] : 100 });
      uploadCreatedFood(`${tmpFood.name} [${userUid}]`, clone);

      window.history.go(-2);
      await wait(50);
      window.history.pushState('', '', '/favorites');
      updateRenderState([
        <Suspense fallback='' key={Math.random() * 100}>
          <FavoritePage key={Math.random() * 100} hashName={window.location.hash.split('#')[1]} />
        </Suspense>,
        <NavicationBar key={Math.random() * 100} />,
      ]);
    }

    storeData();
  };

  trashCanHandle = () => {
    const id = tmpFood.id;
    const arrayIndexInAdded = addedByUsers.findIndex(e => e.id === id);
    addedByUsers.splice(arrayIndexInAdded, 1);
    const arrayIndexInData = data.findIndex(e => e.id === id);
    data.splice(arrayIndexInData, 1);
    userData.favorite.food.splice(userData.favorite.food.indexOf(id), 1);

    // delete all addedByUser items from diary
    for (let index = 0; index < userData.diary.length; index++) {
      const element = userData.diary[index];
      for (const key in element) {
        if (key !== 'date' && key !== 'eaten') {
          const ob = element[key];
          for (let i = ob.length - 1; i >= 0; i--) {
            const el = ob[i];
            if (el[0] === id) ob.splice(i, 1);
          }
          if (ob.length === 0) delete element[key];
        }
      }
    }

    // delete all addedByUser items from meals
    if (userData.favorite.meals.length > 0) {
      for (let index = 0; index < userData.favorite.meals.length; index++) {
        const element = userData.favorite.meals[index];
        for (let i = element.content.length - 1; i >= 0; i--) {
          const el = element.content[i];
          if (el[0] === id) element.content.splice(i, 1);
        }
        if (element.content.length === 0) userData.favorite.meals.splice(index, 1);
      }
    }

    storeData();

    window.history.back();
  };

  toggleConfirm = async () => {
    const getDisplay = S('#createFoodConfirmBG').getStyle('display');
    if (getDisplay === 'none') {
      S('#createFoodConfirmBG').css({ display: 'block' });
      S('#createFoodConfirmContainer').css({ display: 'block' });
      await wait(20);
      S('#createFoodConfirmBG').css({ opacity: 1 });
      S('#createFoodConfirmContainer').css({ opacity: 1, bottom: '50%' });
    } else {
      S('#createFoodConfirmBG').css({ opacity: 0 });
      S('#createFoodConfirmContainer').css({ opacity: 0 });
      await wait(350);
      S('#createFoodConfirmBG').removeAttr('style');
      S('#createFoodConfirmContainer').removeAttr('style');
    }
  };

  backButtonHandle = () => window.history.back();

  render() {
    return (
      <Fragment>
        <div id='createFoodConfirmBG' onClick={this.toggleConfirm}></div>
        <div id='createFoodConfirmContainer'>
          <p>سيتم حذف هذا العنصر من اليوميات وجميع الوجبات . هل أنت متأكد؟</p>
          <button onClick={() => this.trashCanHandle()}>نعم</button>
          <button onClick={this.toggleConfirm}>لا</button>
        </div>
        <div id='createFoodPage'>
          <Snackbar />
          <header id='createFoodPageHeader'>
            <svg id='createFoodBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
              <path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
            </svg>
            <div id='createFoodHeaderButtonsContainer'>
              <svg id='CreateMealSave' onClick={this.doneHandle} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
                <path d='M15.003 3h2.997v5h-2.997v-5zm8.997 1v20h-24v-24h20l4 4zm-19 5h14v-7h-14v7zm16 4h-18v9h18v-9z' />
              </svg>
              <svg
                id='CreateMealTrashCan'
                style={{
                  display: window.location.pathname.includes('/EditFood') ? 'block' : 'none',
                }}
                xmlns='http://www.w3.org/2000/svg'
                viewBox='0 0 24 24'
                onClick={() => this.toggleConfirm()}
              >
                <path d='M3 6v18h18v-18h-18zm5 14c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm5 0c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm5 0c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm4-18v2h-20v-2h5.711c.9 0 1.631-1.099 1.631-2h5.315c0 .901.73 2 1.631 2h5.712z' />
              </svg>
            </div>
          </header>
          <div className='createFoodItemsContainer' id='foodNameContainer'>
            <p className='createFoodTitles'>اسـم الطـعـام</p>
            <input
              id='foodName'
              type='text'
              autoComplete='off'
              defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.name : ''}
              placeholder='اسـم الطـعـام'
              onChange={e => (tmpFood.name = e.target.value)}
              onFocus={() => S('#foodNameContainer').removeAttr('style')}
            ></input>
          </div>
          <div className='createFoodItemsContainer'>
            <p className='createFoodTitles'>العلامة التجارية</p>
            <input
              id='foodBrand'
              type='text'
              autoComplete='off'
              defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.brand : ''}
              placeholder='اسـم العلامة التجارية'
              onChange={e => (tmpFood.brand = e.target.value)}
            />
          </div>
          <div className='createFoodItemsContainer'>
            <p className='createFoodTitles'>اخـتـر الصـنـف</p>
            <select
              id='chooseCategorie'
              className='createFoodInputs'
              defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.description : 'آخر'}
              onChange={e => {
                tmpFood.description = e.target.value;
                this.centerSelect('chooseCategorie');
              }}
            >
              <option value='أسماك'>الأسماك</option>
              <option value='البيض ومنتجات الحليب'>البيض ومنتجات الحليب</option>
              <option value='بقوليات'>البقوليات</option>
              <option value='بذور'>البذور</option>
              <option value='حبوب'>الحبوب</option>
              <option value='البسكوت والحلويات'>البسكوت والحلويات</option>
              <option value='خضار'>الخضروات</option>
              <option value='الدبس والصلصات'>الدبس والصلصات</option>
              <option value='الزيت والدهون'>الزيت والدهون</option>
              <option value='فروج'>الفروج</option>
              <option value='فاكهة'>الفواكه</option>
              <option value='لحوم مصنعة'>اللحوم المصنعة</option>
              <option value='معجنات'>المعجنات</option>
              <option value='مكسرات'>المكسرات</option>
              <option value='مكملات غذائية'>المكملات غذائية</option>
              <option value='آخر'>آخر</option>
            </select>
          </div>
          <div
            className='createFoodItemsContainer'
            style={{ display: window.location.pathname.includes('/EditFood') ? 'none' : 'block' }}
          >
            <p className='createFoodTitles'>قيمة الحصة القياسية</p>
            <p className='createFoodUnits'>غرام</p>
            <input
              id='foodServingValue'
              autoComplete='off'
              inputMode='numeric'
              min='0'
              defaultValue={100}
              onChange={e => {
                e.target.value = toEnglishNumber(e.target.value);
                tmpFood.serving[0][1] = Number(e.target.value);
                tmpFood.serving[0][0] = 'حصة قياسية (' + e.target.value + ')غ';
              }}
              onClick={e => e.target.select()}
            />
          </div>
          <div className='createFoodItemsContainer'>
            <p className='createFoodTitles'>السعرات الحرارية كل 100 غرام</p>
            <p className='createFoodUnits'>سعرة</p>
            <input
              id='foodKcal'
              autoComplete='off'
              inputMode='numeric'
              min='0'
              defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.kcal : 0}
              onChange={e => {
                e.target.value = toEnglishNumber(e.target.value);
                tmpFood.kcal = Number(e.target.value);
              }}
              onClick={e => e.target.select()}
            />
          </div>
          <div id='createFoodNutritionInfoDetail'>
            <div id='createFoodNutritionInfoTitles'>
              <p className='createFoodNutritionInfoMain'>البروتين</p>
              <p className='createFoodNutritionInfoMain'>الكربوهيدرات</p>
              <p className='createFoodNutritionInfoSub'>الألياف</p>
              <p className='createFoodNutritionInfoSub'>السكر</p>
              <p className='createFoodNutritionInfoMain'>الدهون</p>
              <p className='createFoodNutritionInfoSub'>الدهون المشبعة</p>
              <p className='createFoodNutritionInfoSub'>الدهون الغير مشبعة</p>
              <p className='createFoodNutritionInfoMain'>آخر</p>
              <p className='createFoodNutritionInfoSub'>الكوليسترول</p>
              <p className='createFoodNutritionInfoSub'>الصوديوم</p>
              <p className='createFoodNutritionInfoSub'>البوتاسيوم</p>
            </div>
            <div id='createFoodNutritionInfoValues'>
              <p className='createFoodNutritionInfoMain'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.protein : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.protein = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoMain'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.carb : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.carb = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoSub'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.fiber : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.fiber = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoSub'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.sugar : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.sugar = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoMain'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.fat : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.fat = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoSub'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.saturatedFat : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.saturatedFat = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoSub'>
                غ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.unsaturatedFat : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.unsaturatedFat = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoMain' style={{ color: 'rgb(0, 0, 0, 0)' }}>
                ......
              </p>
              <p className='createFoodNutritionInfoSub'>
                ملغ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.cholesterol : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.cholesterol = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoSub'>
                ملغ{' '}
                <input
                  inputMode='numeric'
                  autoComplete='off'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.sodium : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.sodium = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
              <p className='createFoodNutritionInfoSub'>
                ملغ{' '}
                <input
                  autoComplete='off'
                  inputMode='numeric'
                  min='0'
                  defaultValue={window.location.pathname.includes('/EditFood') ? tmpFood.potassium : 0}
                  onChange={e => {
                    e.target.value = toEnglishNumber(e.target.value);
                    tmpFood.potassium = Number(e.target.value);
                  }}
                  onClick={e => e.target.select()}
                />
              </p>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}
